import { Component, OnInit, Input } from '@angular/core';
import { FetchDataService } from '../fetch-data.service';

@Component({
  selector: 'app-searched',
  templateUrl: './searched.component.html',
  styleUrls: ['./searched.component.css']
})
export class SearchedComponent implements OnInit {

  public number_of_countries;
  public array_of_countries=[];
  @Input() public parentData;
  public searchedCountry='';
  public display={};

  constructor(private _fetchData: FetchDataService) { }

  ngOnInit() {
    this._fetchData.getData()
    .subscribe(data=>{

      this.number_of_countries = Object.values(Object.values(Object.values(data))[1]).length;
      console.log(this.number_of_countries)
      for(let j=0; j<this.number_of_countries;j++) {
        var mySmallObj = {
          'country': Object.values(Object.values(Object.values(data)[1])[j])[0],
          'confirmed' : Object.values(Object.values(Object.values(data)[1])[j])[4],
          'new_confirmed' : Object.values(Object.values(Object.values(data)[1])[j])[3],
          'deaths' : Object.values(Object.values(Object.values(data)[1])[j])[6],
          'new_deaths' : Object.values(Object.values(Object.values(data)[1])[j])[5],
          'recovered' : Object.values(Object.values(Object.values(data)[1])[j])[8],
          'new_recovered' : Object.values(Object.values(Object.values(data)[1])[j])[7],
          'slug' : Object.values(Object.values(Object.values(data)[1])[j])[2]
        };
        
        this.array_of_countries.push(mySmallObj);
    }
    })
    // searching the country
    for(let i=0;i<this.number_of_countries;i++){
      // converting to uppercase to implement case insensitivity in string comparison
      console.log(this.array_of_countries[i]);
      if(this.searchedCountry.toUpperCase() == this.array_of_countries[i].slug.toUpperCase()){
        this.display = this.array_of_countries[i];
        console.log(this.display);
        break;
      }
      continue;
    }
    // var countryCode = this.countries.getName(this.display.country,"en");
    // console.log(countryCode);
  }

}
