import { Component, OnInit } from '@angular/core';
import { FetchDataService } from '../fetch-data.service';
import { ChartType } from 'chart.js';
import { MultiDataSet, Label, Color} from 'ng2-charts';


@Component({
  selector: 'app-global',
  templateUrl: './global.component.html',
  styleUrls: ['./global.component.css']
})
export class GlobalComponent implements OnInit {

  public fetched_data = [];
  public global_data = [];
  public total_active: number;
  public new_active: number;
  public total_deaths: number;
  public new_deaths: number;
  public total_recovered: number;
  public new_recovered: number;
  public total_cases: number;
  public new_cases: number;
  public percentage_active: number;
  public percentage_recovered: number;
  public percentage_deaths: number;
  public array_of_countries = [];
  public number_of_countries;
  public sorted_array=[];
  public object_for_table=[];
  public reversed_array = [];
  public top_ten = [];
  public displayedColumns: string[] = ['position','country', 'confirmed'];
  public dataSource;
  public test: boolean = true;

  constructor(private _fetchData: FetchDataService) { }
  public pieChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
}
public pieChartLabels =  ['ACTIVE', 'RECOVERED', 'DEATHS'];
public pieChartColor:Color[] = [{
  backgroundColor: ['#FF073A','#32BF84','#95A3A6']
}];
public pieChartData:any = [];
public barGraphData: any = [];
public pieChartLegend = true;
public pieChartType = 'doughnut';
  ngOnInit() {
    this._fetchData.getData()
    .subscribe(data => {
      // this generates an array containing global data; global_data
      for(let i = 0; i<6; i++){
        this.global_data[i] = Object.values(Object.values(data)[0])[i];
      }
      
      this.number_of_countries = Object.values(Object.values(Object.values(data))[1]).length;
      // generates an array of arrays containing name of countries & total confirmed cases
      
      for(let j=0; j<this.number_of_countries;j++) {
          var mysmallArray = new Array(2);
          mysmallArray[0] = Object.values(Object.values(Object.values(data)[1])[j])[0];
          mysmallArray[1] = Object.values(Object.values(Object.values(data)[1])[j])[4];
          this.array_of_countries.push(mysmallArray);
      }

      for(let j=0; j<this.number_of_countries;j++) {
        var mySmallObj = {
          'country': Object.values(Object.values(Object.values(data)[1])[j])[0],
          'confirmed' : Object.values(Object.values(Object.values(data)[1])[j])[4]
        };
        
        this.object_for_table.push(mySmallObj);
    }
      
      this.object_for_table.sort(function(a,b){return a.confirmed-b.confirmed});
      // console.log(this.object_for_table.reverse()[3]);
      this.reversed_array = this.object_for_table.reverse();
      for(let i=0;i<10;i++){
        this.top_ten.push(this.reversed_array[i]);
      }
      this.dataSource = this.top_ten;
      // console.log(this.top_ten);
      for(let i=0;i<10;i++){
        this.top_ten[i].position = i+1;
      }
      console.log(this.top_ten);
      this.new_active = this.global_data[0] - this.global_data[2] - this.global_data[4];
      this.total_active = this.global_data[1] - this.global_data[3] - this.global_data[5];
      this.new_deaths = this.global_data[2];
      this.total_deaths = this.global_data[3];
      this.new_recovered = this.global_data[4];
      this.total_recovered = this.global_data[5];
      this.new_cases = this.global_data[0];
      this.total_cases = this.global_data[1];
      this.percentage_active = (this.total_active/this.total_cases)*100;
      this.percentage_recovered = (this.total_recovered/this.total_cases)*100;
      this.percentage_deaths = (this.total_deaths/this.total_cases)*100;
      this.pieChartData = [this.percentage_active.toFixed(2),this.percentage_recovered.toFixed(2),this.percentage_deaths.toFixed(2)];
      // this.pieChartData = [Math.floor(this.percentage_active),Math.floor(this.percentage_recovered),Math.floor(this.percentage_deaths)];
      // this.pieChartData = [50,25,25];
      if(this.new_active<0){this.test=false;}
    });
    console.log(typeof(this.percentage_active));
    // this.pieChartData = [this.percentage_active.toFixed(2),this.percentage_recovered.toFixed(2),this.percentage_deaths.toFixed(2)];
    // console.log(this.pieChartData);

    var comparator = function (a,b){
      return a[1]-b[1];
    };
    // console.log(this.array_of_countries);
    
  }
  
}
