import { Component, OnInit } from '@angular/core';
import { FetchDataService } from '../fetch-data.service';
import { DataInterface } from '../data-interface';
import { ChartType } from 'chart.js';
import { MultiDataSet, Label, Color} from 'ng2-charts';

@Component({
  selector: 'app-country',
  templateUrl: './country.component.html',
  styleUrls: ['./country.component.css']
})
export class CountryComponent implements OnInit {
  public number_of_countries;
  public array_of_countries=[];
  public selectedCountry='';
  public display;
  public test:boolean = false;
  // public countries = require("i18n-iso-countries");
  public pieChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
}
public pieChartLabels =  ['ACTIVE', 'RECOVERED', 'DEATHS'];
public pieChartColor:Color[] = [{
  backgroundColor: ['#FF073A','#32BF84','#95A3A6']
}];
public pieChartData:any = [];
public pieChartLegend = true;
public pieChartType = 'doughnut';
public percentage_active: number;
public percentage_recovered: number;
public percentage_deaths: number;
public test1: boolean = true;
public active: number;
public new_active: number;


  constructor(private _fetchData: FetchDataService) { }

  ngOnInit() {
    this._fetchData.getData()
    .subscribe(data=>{

      this.number_of_countries = Object.values(Object.values(Object.values(data))[1]).length;
      
      for(let j=0; j<this.number_of_countries;j++) {
        var mySmallObj: DataInterface = {
          country: Object.values(Object.values(Object.values(data)[1])[j])[0],
          confirmed : Object.values(Object.values(Object.values(data)[1])[j])[4],
          new_confirmed : Object.values(Object.values(Object.values(data)[1])[j])[3],
          deaths : Object.values(Object.values(Object.values(data)[1])[j])[6],
          new_deaths : Object.values(Object.values(Object.values(data)[1])[j])[5],
          recovered : Object.values(Object.values(Object.values(data)[1])[j])[8],
          new_recovered : Object.values(Object.values(Object.values(data)[1])[j])[7],
          slug : Object.values(Object.values(Object.values(data)[1])[j])[2],
          
          // new_active : (mySmallObj.new_confirmed - mySmallObj.new_recovered - mySmallObj.new_deaths)
        };
        this.array_of_countries.push(mySmallObj); 
        
    }
    // console.log(this.active);
    })
  }
  loadData(){
    
    console.log(this.selectedCountry);
    
    // searching the country
    for(let i=0;i<this.number_of_countries;i++){
      
      if(this.selectedCountry.toUpperCase() == this.array_of_countries[i].country.toUpperCase()){
        
        this.active =  this.array_of_countries[i].confirmed - this.array_of_countries[i].recovered - this.array_of_countries[i].deaths;
        this.new_active = this.array_of_countries[i].new_confirmed - this.array_of_countries[i].new_recovered - this.array_of_countries[i].new_deaths;
        if(this.new_active<0){this.test1=false;}
        this.percentage_active = (this.active/this.array_of_countries[i].confirmed)*100;
        this.percentage_recovered = (this.array_of_countries[i].recovered/this.array_of_countries[i].confirmed)*100;
        this.percentage_deaths = (this.array_of_countries[i].deaths/this.array_of_countries[i].confirmed)*100;
        this.display = this.array_of_countries[i];
        this.pieChartData =  [this.percentage_active.toFixed(2),this.percentage_recovered.toFixed(2),this.percentage_deaths.toFixed(2)];
        this.test =true;
        
        break;
      }
      continue;
      
    }
    
  }

  
}
